body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
}
/* MENUBAR */
#menubar-container {
    padding: 20px;
    text-align: left;
    position: absolute;
    right: 0px;
}

#menubar-container div {
    margin: auto auto auto 100px;
}

#menubar-container a {
    text-decoration: none;
    color: rgb(37, 37, 37);
    text-shadow: .4px .4px black;
    padding: 3px;
    margin: 10px;
    border-bottom: 1px solid transparent;
    transition: 0.2s;
}

#menubar-container a:hover {
    text-decoration: none;
    border-bottom: 1px solid rgb(43, 43, 43);
    transition: 0.2s;
}

#menubar-container a:focus {
    text-decoration: none;
    border-bottom: 1px solid rgb(43, 43, 43);
}

/* FOOTER */
footer {
    position: relative;
    text-align: center;
    background-color: rgb(211, 211, 211);
    width: 100%;
    margin-top: 135px;
    height: 150px;
}

footer a {
    text-decoration: none;
    color: rgb(43, 43, 43);
    padding: 5px;
    font-size: 25px;
}

footer a:focus {
    text-decoration: none;
    color: rgb(43, 43, 43);
}

/* HOME */
#home-body-container {
    padding: 200px 50px 50px 50px;
    display: flex;
    justify-content: space-evenly;
}

#home-body-container div h1 {
    margin: 3px auto 3px auto;
    color: rgb(37, 37, 37);
    font-size: 55px;
}

#home-body-container div h3 {
    margin: 3px auto 3px auto;
    color: rgb(37, 37, 37);
    font-size: 45px;
}

#home-body-container div p {
    color: rgb(37, 37, 37);
    margin-top: 2px;
    font-size: 20px;
}

#home-body-container div img {
    width: 30%;
}

/* ABOUT */
#about-container {
    padding: 50px;
}

#about-container div:nth-child(1) {
    margin: auto;
    width: 50%;
}

#about-container #about-header {
    font-size: 26px;
}

#about-container #about-text {
    font-size: 20px;
}

/* PROJECTS */
#project-container {
    padding: 50px;
}

#project-container #project-items {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    padding: 30px;
}

.project-item {
    text-decoration: none;
    color: rgb(49, 49, 49);
    padding: 20px;
    border: 1px solid rgba(133, 133, 133, 0.5);
}

.project-item:focus {
    text-decoration: none;
    color: rgb(49, 49, 49);
}

.project-item p {
    margin-top: 2px;
    margin-bottom: 2px;
}

.project-item a {
    text-decoration: none;
    color: rgb(77, 116, 161);
}

.project-item a:focus {
    text-decoration: none;
    color: rgb(77, 116, 161);
}

/* SKILLS */
#skills-container {
    display: flex;
    justify-content: center;
    padding: 50px;
}

#skills-container ul {
    list-style: none;
    padding-left: 3px;
    margin-top: 1px;
}

#skills-container #databases, #technologies, #languages {
    color: rgb(49, 49, 49);
    padding: 20px;
    border: 1px solid rgba(133, 133, 133, 0.5);
    margin: 20px;
}
